import React from 'react';

export default function Zone (props) {
    const onZoneClick = (e) => {
        props.onZoneClicked && props.onZoneClicked(props.id);
    };

    return (
        <div style={{ width: '100px', height: '100px', backgroundColor: 'red', margin: '5px' }} onClick={onZoneClick} />
    );
}


