import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export default function ConfirmationDialogComponent(props) {
    const [open, setOpen] = React.useState(props.open);

    useEffect(() => setOpen(props.open), [props.open]);

    const handleAgree = () => {
        setOpen(false);
        if (props.agree) props.agree();
    };

    const handleDisagree = () => {
        setOpen(false);
        if (props.disagree) props.disagree();
    };

    return (
        <div>
          <Dialog
            open={open}
            onClose={handleDisagree}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            <DialogTitle id="alert-dialog-title">{"Supprimer"}</DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                Supprimer le matériau?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleDisagree} color="primary" autoFocus>
                Annuler
              </Button>
              <Button onClick={handleAgree} color="primary">
                Supprimer
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      );
}


