import Zone from './zone.function';
import React, { useEffect } from 'react';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Slide from '@material-ui/core/Slide';
import { fade, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import CloseIcon from '@material-ui/icons/Close';
import MaterialList from './material-list.function';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import materialService from '../services/material.service';
import zoneService from '../services/zone.service';

import MapComponent from '../components/map.component';
import SearchComponent from '../components/search.component';

import Grid from '@material-ui/core/Grid';


const useStyles = makeStyles(theme => ({
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },

    grow: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        display: 'none',
        [theme.breakpoints.up('sm')]: {
            display: 'block',
        },
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
    },
    sectionDesktop: {
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },

    gridContainer: {
        margin: '8px',
        width: 'inherit'
    }
}));

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function InteractiveMap() {
    const [datasource, setDatasource] = React.useState([]);
    const [filteredDatasource, setFilteredDatasource] = React.useState(datasource);
    const [currentZoneId, setCurrentZoneId] = React.useState();
    const [zones, setZones] = React.useState([]);
    const [materials, setMaterials] = React.useState([]);

    useEffect(() => {
        (async () => {
            setZones((await zoneService.getZones()).response);
        })();
    }, []);

    useEffect(() => {
        (async () => {
            const materials = await materialService.getMaterials();
            setMaterials(materials);
        })();
    }, []);

    /**
     * Handles material favorite event.
     * 
     * @param {*} e The event.
     */
    const onFavorite = (e) => {

        filteredDatasource.forEach(async item => {
            if (item.key === e) {
                (async () => {
                    await materialService.addMaterial(item.key, currentZoneId);
                })();
                item.favorite = true;
            }
        });

        setFilteredDatasource([...filteredDatasource]);
    };

    /**
     * Handles material unfavorite event.
     * 
     * @param {*} e The event.
     */
    const onUnfavorite = (e) => {
        filteredDatasource.forEach(item => {
            if (item.key === e) {
                (async () => {
                    await materialService.deleteMaterial(item.key, currentZoneId);
                })();
                item.favorite = false;
            }
        });
        setFilteredDatasource([...filteredDatasource]);
    };

    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    /**
     * Handles dialog close event.
     */
    const onDialogClose = () => {
        setOpen(false);
    };

    /**
     * Opens the dialog with the given zone identifier.
     * 
     * @param {*} zoneId The zone identifier.
     */
    const openDialog = async (zoneId) => {
        const zoneMaterials = await materialService.getZoneMaterials(zoneId);
        const allMaterials = await materialService.getMaterials();

        allMaterials.forEach(material => {
            if (zoneMaterials.find(zoneMaterial => zoneMaterial.key === material.key)) {
                material.favorite = true;
            } else {
                material.favorite = false;
            }
        });

        setDatasource(allMaterials);
        applyFilter();
        setOpen(true);
        setCurrentZoneId(zoneId);
    };

    /**
     * Applies datasource and filter to the filtered datasource.
     * 
     * @param {*} filter The filter.
     */
    const applyFilter = (filter) => {
        if (filter) {
            setFilteredDatasource(
                datasource.filter(item => {
                    return item.name.toLowerCase().indexOf(filter.toLowerCase()) > -1;
                })
            );
        } else {
            setFilteredDatasource(datasource);
        }
    }

    /**
     * On filter input change.
     * 
     * @param {*} event The event.
     */
    const onFilterChange = (event) => {
        applyFilter(event.target.value);
    };

    /**
     * Apply filter to the filtered data source when the datasource is updated.
     */
    useEffect(applyFilter, [datasource]);

    return (
        <div>
            {/*<Zone id="1" onZoneClicked={openDialog} />*/}

            <Grid container className={classes.gridContainer} spacing={3}>
                <Grid item xs={9}>
                    <MapComponent />
                </Grid>
                <Grid item xs={3}>
                    <SearchComponent materials={materials} />
                </Grid>
            </Grid>



            {/*

            <List>
                {
                    zones.map(item => (
                        <ListItem key={item.id} onClick={() => openDialog(item.id)}>
                            <ListItemText className={"noselect"} primary={item.name} />
                        </ListItem>
                    ))
                }
            </List>
            <div>
                <Dialog fullScreen open={open} onClose={onDialogClose} TransitionComponent={Transition}>
                    <div className={classes.grow}>
                        <AppBar position="static">
                            <Toolbar>
                                <IconButton edge="start" color="inherit" onClick={onDialogClose} aria-label="close">
                                    <CloseIcon />
                                </IconButton>
                                <Typography className={classes.title} variant="h6" noWrap>Matériaux</Typography>
                                <div className={classes.search}>
                                    <div className={classes.searchIcon}>
                                        <SearchIcon />
                                    </div>
                                    <InputBase
                                        onChange={onFilterChange}
                                        placeholder="Filtrer"
                                        classes={{
                                            root: classes.inputRoot,
                                            input: classes.inputInput,
                                        }}
                                        inputProps={{ 'aria-label': 'search' }}
                                    />
                                </div>
                                <div className={classes.grow} />
                            </Toolbar>
                        </AppBar>

                        <MaterialList datasource={filteredDatasource} onFavorite={onFavorite} onUnfavorite={onUnfavorite}></MaterialList>
                    </div>
                </Dialog>
            </div>
                                    */}
        </div>
    );
}


