import React from 'react';

import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import MaterialListItem from './material-list-item.function';

export default function MaterialList(props) {

    const getSubList = (favorite) => {
        return props.datasource.filter(item => {
            return item.favorite === favorite;
        });
    };

    const createMaterialListItem = (item) => {
        return <MaterialListItem onFavorite={() => onFavorite(item.key)} onUnfavorite={() => {onUnfavorite(item.key)}} key={item.key} name={item.name} favorite={item.favorite} />
    }

    const onFavorite = (key) => {
        props.onFavorite && props.onFavorite(key);
    };

    const onUnfavorite = (key) => {
        props.onFavorite && props.onUnfavorite(key);
    };

    return (
        <List>
            {getSubList(true).map(createMaterialListItem)}
            <Divider key="materialListItemDivider1" />
            {getSubList(false).map(createMaterialListItem)}
        </List>
    );
}


