import {environment} from '../environment/environment';

module.exports = {
    addMaterial: async function(materialId, zoneId) {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' }
        };
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/zones/${zoneId}/materials/${materialId}`, requestOptions);
        const json = await response.json();

        return json;
    },

    deleteMaterial: async function(materialId, zoneId) {
        const requestOptions = {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' }
        };
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/zones/${zoneId}/materials/${materialId}`, requestOptions);
        const json = await response.json();

        return json;
    },

    getZoneMaterials: async function(zoneId) {
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/zones/${zoneId}/materials`);
        const json = await response.json();

        return json.response.map(item => {
            return {
              key: item.id,
              name: item.name,
              favorite: true  
            };
        });
    },

    getMaterials: async function() {
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials`);
        const json = await response.json();

        return json.response.map(item => {
            return {
              key: item.id,
              name: item.name,
              uuid: item.uuid
            };
        });
    },

    create: async function(material) {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                name: material.name
            })
        };
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials`, requestOptions);
        const json = await response.json();

        return json.response.map(item => {
            return {
              key: item.id,
              name: item.name,
              uuid: item.uuid
            };
        })[0];
    },

    delete: async function(material) {
        const requestOptions = {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' }
        };

        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials/${material.uuid}`, requestOptions);
        const json = await response.json();

        return json;
    },

    update: async function(material) {
        const requestOptions = {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                name: material.name
            })
        };
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials/${material.uuid}`, requestOptions);
        const json = await response.json();

        return json.response.map(item => {
            return {
              key: item.id,
              name: item.name,
              uuid: item.uuid
            };
        });
    }
}


