import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import StarIcon from '@material-ui/icons/Star';
import StarBorder from '@material-ui/icons/StarBorder';

export default function MaterialListItem(props) {
    return (
        <ListItem>
            <ListItemText className={"noselect"} primary={props.name} />
            <ListItemSecondaryAction>
                <IconButton edge="start" onClick={props.favorite ? props.onUnfavorite : props.onFavorite}>
                    {props.favorite ? <StarIcon /> : <StarBorder />}
                </IconButton>
            </ListItemSecondaryAction>
        </ListItem>
    );
}


