import React, { useEffect } from 'react';
import materialService from '../services/material.service';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import { makeStyles } from '@material-ui/core/styles';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Grid from '@material-ui/core/Grid';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';

import IconButton from '@material-ui/core/IconButton';

import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';

import MaterialFormDialogComponent from './material-form-dialog.component';
import ConfirmationDialogComponent from './confirmation-dialog.component';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
    actionIcons: {
        textAlign: 'right'
    },
    listitem: {
        paddingLeft: '32px'
    },
    fab: {
        position: 'absolute',
        bottom: theme.spacing(2),
        right: theme.spacing(2),
      }
}));

export default function MaterialsComponent() {
    const [dialogOpen, setDialogOpen] = React.useState(false);
    const [deleteConfirmationOpen, setDeleteConfirmationOpen] = React.useState(false);
    const [materials, setMaterials] = React.useState([]);
    const [materialToEdit, setMaterialToEdit] = React.useState({name: ''});
    const [materialToDelete, setMaterialToDelete] = React.useState(undefined);

    const openDialog = () => {
        setDialogOpen(true)
    };

    const onDialogClose = (material) => {
        setDialogOpen(false);
        if (material) {
            if (!material.uuid) {
                createMaterial(material);
            } else {
                updateMaterial(material);
            }
        }
    };

    const openDeleteMaterialDialog = (material) => {
        setDeleteConfirmationOpen(true);
        setMaterialToDelete(material);
    };

    const createMaterial = async (material) => {
        const createdMaterial = await materialService.create(material);
        setMaterials([...materials, createdMaterial])
    };

    const deleteMaterial = async (material) => {
        await materialService.delete(material);
        setMaterials([...materials.filter(m => m.uuid !== material.uuid)]);
    };

    const updateMaterial = async (material) => {
        await materialService.update(material);
        materials.find(m => m.uuid === material.uuid).name = material.name;
        setMaterials([...materials]);

    };

    const editMaterial = (material) => {
        setMaterialToEdit({
            uuid: material.uuid,
            name: material.name
        });
        setDialogOpen(true);
    };

    const onFabClick = () => {
        setMaterialToEdit({name: ''});
        openDialog();
    };

    const onAgree = async () => {
        await deleteMaterial(materialToDelete);
        setDeleteConfirmationOpen(false);
    };

    const onDisagree = () => {
        setDeleteConfirmationOpen(false);
    };

    /* Load materials. */
    useEffect(() => {
        (async () => setMaterials(await materialService.getMaterials()))();
    }, []);

    const classes = useStyles();
    return (
        <div className={classes.root}>
            <MaterialFormDialogComponent open={dialogOpen} onClose={onDialogClose} material={materialToEdit} />
            <ConfirmationDialogComponent open={deleteConfirmationOpen} agree={onAgree} disagree={onDisagree} />
                <List component="nav">
                    {
                        materials.map(material => (
                            <ListItem className={classes.listitem} key={material.key}>
                                    <ListItemText className={"noselect"} primary={material.name} />
                                <div className={classes.actionIcons}>

                                    <IconButton edge="start" onClick={() => editMaterial(material)}>
                                        <EditIcon />
                                    </IconButton>
                                    <IconButton edge="start" onClick={() => openDeleteMaterialDialog(material)}>
                                        <DeleteIcon />
                                    </IconButton>
                                </div>
                            </ListItem>
                        ))
                    }
                </List>
            <Fab className={classes.fab} color="primary" onClick={onFabClick}>
                <AddIcon />
            </Fab>
        </div>
    );
}


