import React, { useEffect } from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import SearchIcon from '@material-ui/icons/Search';
import InputBase from '@material-ui/core/InputBase';
import MaterialLocalizationComponent from './material-localization.component';
import Divider from '@material-ui/core/Divider';

const useStyles = makeStyles((theme) => ({
    container: {
        height: '100%',
        width: '100%'
    },
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        whiteSpace: 'nowrap',
        marginBottom: theme.spacing(1),
    },
    searchIcon: {
        position: 'absolute',
        pointerEvents: 'none',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
    },
    search: {
        display: 'flex',
        alignItems: 'center',
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: 'auto',
        }
    },
}));
export default function SearchComponent(props) {
    const classes = useStyles();
    const [materials, setMaterials] = React.useState(props.materials);
    const [filteredMaterials, setFilteredMaterials] = React.useState(props.materials);

    useEffect(() => {
        setMaterials(props.materials);
    }, [props.materials]);

    useEffect(() => {
        setFilteredMaterials(materials);
    }, [materials]);

    /**
     * Filters the list of materials.
     * 
     * @param {*} filter The filter.
     */
    const applyFilter = (filter) => {
        if (filter) {
            setFilteredMaterials(
                materials.filter(item => {
                    return item.name.toLowerCase().indexOf(filter.toLowerCase()) > -1;
                })
            );
        } else {
            setFilteredMaterials(materials);
        }
    }

    /**
     * On filter input change.
     * 
     * @param {*} event The event.
     */
    const onFilterChange = (event) => {
        applyFilter(event.target.value);
    };

    return (
        <div className={classes.container}>
            <Paper className={classes.paper}>
                <div className={classes.search}>
                    <div className={classes.searchIcon}>
                        <SearchIcon />
                    </div>
                    <InputBase
                        onChange={onFilterChange}
                        placeholder="Rechercher"
                        classes={{
                            root: classes.inputRoot,
                            input: classes.inputInput,
                        }}
                        inputProps={{ 'aria-label': 'search' }}
                    />
                </div>
                <Divider />
                <MaterialLocalizationComponent materials={filteredMaterials}></MaterialLocalizationComponent>
            </Paper>
        </div>
    );
}


