import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '300px'
    }
}));

export default function MaterialFormDialogComponent(props) {
    const [open, setOpen] = React.useState(props.open);
    const [material, setMaterial] = React.useState(props.material);

    useEffect(() => setOpen(props.open), [props.open]);
    useEffect(() => setMaterial(props.material), [props.material]);

    const handleClose = () => {
        setOpen(false);
        if (props.onClose) props.onClose(material);
    };

    const handleCancel = () => {
        if (props.onClose) props.onClose(undefined);
    };

    const onNameChange = (e) => {
        material.name = e.target.value;
        setMaterial(material);
    };

    const classes = useStyles();

    return (
        <div>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle id="form-dialog-title">{material.uuid ? 'Modification' : 'Ajout'} d'un matériau</DialogTitle>
                <DialogContent className={classes.root}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Nom"
                        fullWidth
                        defaultValue={material.name}
                        onChange={onNameChange}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCancel} color="primary">
                        Annuler
                    </Button>
                    <Button onClick={handleClose} color="primary">
                        {material.uuid ? 'Modifier' : 'Ajouter'}
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}


